







































import {Component, Vue} from "vue-property-decorator";
import UIRepository from "@/repositories/ui-repository";

@Component
export default class MainMenuComponent extends Vue {
    public readonly name: string = "main-menu-component"
    private readonly ClassHasOpen: string = "component-menu__active"

    // <editor-fold defaultstate="collapsed" desc="Vue Hooks">
    private created(): void {
        console.log("main-menu-component - started")
        UIRepository.CurrentMainMenu = this
    }

    // </editor-fold>

    private HasOpen(): boolean {
        return UIRepository.HasOpenMainMenu
    }

    private GetClassHasOpen(): string {
        return (this.HasOpen() ? ' ' + this.ClassHasOpen : '')
    }

    private OnClickToolbarOpenMenu(env: Event): void {
        env.preventDefault()
        UIRepository.OnMainMenuToggle()
    }

    private OnSwipeOpenMenu(): void {
        UIRepository.OnMainMenuToggle()
    }
}
